  .menu {
    background: #ffffff;
    border-radius: 8px;
    position: absolute;
    top: 45px;
    left: 0;
    width: 300px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.3);
    opacity: 0;
    visibility: hidden;
    transform: translateY(-20px);
    transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
    &__container {
        margin-top: 20px;
        position: relative;
      }
      &.active {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
      &__item{
        border-bottom: 1px solid #dddddd;
        text-decoration: none;
        color: #333333;
        padding: 15px 20px;
        display: block;
      }
  }
  

